import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    headerTitle: '',
    balance: 0,
    history: []
  },
  mutations: {
    changeTitle (state, val) {
      state.headerTitle = val
    },
    setBalance (state, val) {
      state.balance = val
    },
    setHistory (state, val) {
      state.history = val
    }
  },
  actions: {
    changeTitle (context, val) {
      context.commit('changeTitle', val)
    },
    setBalance (context, val) {
      context.commit('setBalance', val)
    },
    setHistory (context, val) {
      context.commit('setHistory', val)
    }
  },
  getters: {
    changeTitle: state => state.headerTitle,
    Balance: state => state.balance,
    History: state => state.history
  }
})
