<template>
  <div v-if="windowWidth <= 640" id="app">
    <div v-if="Balance" class="wrapper">
      <Navigation></Navigation>
      <router-view/>
    </div>
    <div v-else class="loading">
      <img id="loadingSpinner" :src="imgPath('Spinner.gif')" alt="">
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import { mapGetters } from 'vuex'
import { db } from './db'
import Navigation from '@/components/Navigation.vue'

export default {
  data () {
    return {
      title: 'History',
      windowWidth: document.body.clientWidth
    }
  },
  computed: {
    ...mapGetters(['Balance'])
  },
  components: {
    Navigation
  },
  methods: {
    imgPath (pic) {
      return require('./assets/' + pic)
    }
  },
  created () {
    db.ref('balance').once('value', snapshot => {
      this.$store.dispatch('setBalance', snapshot.val())
    })
    db.ref('history').once('value', snapshot => {
      this.$store.dispatch('setHistory', snapshot.val())
    })
  },
  mounted () {
    window.addEventListener('resize', () => {
      this.windowWidth = document.body.clientWidth
    })
  }
}
</script>

<style lang="scss">
body{
  margin: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  .loading{
    position: relative;
    width: 100vw;
    height: 100vh;
    #loadingSpinner{
      position: absolute;
      top: calc(50% - 32px);
      left: calc(50% - 32px);
    }
  }
}
</style>
